@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;700&display=swap);
:root{
    font-size: 60%;
    --color-gray: #dadadb;
    --color-dark-gray: #1f2025;
    --color-background: #1F2025 ;
    --gradient-gray: #343741;
    --gradient-green: #b8f087;
    --color-boxshadow: rgba(0,0,0,0.3);
    --base-font:  'Rubik', sans-serif;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box ;
    letter-spacing: calc(12px/10)
}

html, body, #root{
    width: 100%;
    overflow-x: hidden;
    height: 100%;
    color: #9fc883;
    background: #11998e;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#root{
    display: grid;
    place-items: center;
}


body, input, button, textarea {
    font-family: var(base-font);
}

.hidden {
    display: none !important;
}

@media ( min-width: 700px  ){
    :root{
        font-size: 62.5%;
    }
} 

section#display-container p{
    font-size: 4rem;
}


.break-buttons,
.timelenght-buttons
{
    text-align: center;
    font-size:  1.6rem;
    font-weight: normal;
    color: rgba(255, 255, 255, 0.3) !important;
}

.break-buttons h3,
.timelenght-buttons h3{
    font-size:  1.4rem;
    font-weight: normal;
}

.break-buttons span,
.timelenght-buttons span{
    color: white;
}

.break-buttons button,
.timelenght-buttons button{
    padding: 0.2rem;
    border-radius: 3px;
    margin: 0.5rem;
    color: rgba(255, 255, 255, 0.3) !important;

}

.btn-setting{ 
    background: none;
    border: 1px solid rgba(255, 255, 255, 0.3) !important;;
}




button#main-button{
    font-size: 1.6rem;
    border: none;
    color : #fff;
    padding: 1rem 1rem;
    display: inline-block;
    width: 100%;
    background: #11998e;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #38ef7d, #11998e);
}


main.container{
    background : var(--color-background);
    text-align: center;
    display: grid;
    grid-template-columns: repeat(1fr,3);
    grid-template-rows: repeat(1fr, 7);
    box-shadow: 0 16px 28px 0 rgba(0,0,0,.8),0 25px 55px 0 rgba(0,0,0,0.21);
    min-width: 290px;
    min-height: 420px;
}

main.container h1{
    grid-row: 1/2;
    text-align: center;
    font-weight: normal;
    font-size: 2rem;
    padding : 1rem;
    margin-top: 1rem;
   
}

main.container #display{
    border-top: 1px solid rgba(0,0,0, 0.2);
    border-bottom: 1px solid rgba(0,0,0, 0.2);
    grid-row: 2/5;
    min-height: 15rem;
    display: flex;
    align-items: center;
    justify-content: center;

}

main.container #setting-buttons{
    display: grid;
    grid-template-columns: 50% 50%;
    
}

main.container #start-rest-container{
   display: flex;
}

.break-buttons, .timelenght-buttons{
    border: 1px solid rgba(0,0,0, 0.2);
}
